@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@800&family=Source+Sans+Pro:wght@600&display=swap');

.content{
    align-items: center;
    display: inline-flex;
    height: calc(100vh - 60px);
    flex-direction: column;
    width: 100%;
}
.bg1{
    background: url("https://i.ibb.co/N2xcJCD/INTRO-FONDO.jpg");
    background-position-x: center;
    background-position-y: bottom;
    background-size: cover;
    background-repeat: no-repeat;
}
.bg2{
    background: url("https://i.ibb.co/gTMnvk0/OBJETIVO-FONDO-COMPLETO.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
    background-position-y: bottom;
}
.bg3{
    background-color: #a569d1;
}
.bg4{
    background-color: #eeeeee;
}