@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@800&family=Source+Sans+Pro:wght@600&display=swap);
body{background-color:black;border:0;color:#5a5a5a;margin:0;width:100%}.content{height:calc(100vh - 60px);display:-webkit-inline-flex;display:inline-flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}a{text-decoration:none}a:visited{color:#303131;border:1px solid black}.separator{background-color:#383838;width:100%;height:1px}.bg-1{background-color:#eeeeee}.bg-2{background-color:#a569d1}

.content{-webkit-align-items:center;align-items:center;display:-webkit-inline-flex;display:inline-flex;height:calc(100vh - 60px);-webkit-flex-direction:column;flex-direction:column;width:100%}.bg1{background:url("https://i.ibb.co/N2xcJCD/INTRO-FONDO.jpg");background-position-x:center;background-position-y:bottom;background-size:cover;background-repeat:no-repeat}.bg2{background:url("https://i.ibb.co/gTMnvk0/OBJETIVO-FONDO-COMPLETO.png");background-repeat:no-repeat;background-size:cover;background-position-x:center;background-position-y:bottom}.bg3{background-color:#a569d1}.bg4{background-color:#eeeeee}

.card{display:inline-block;margin-top:10px;color:black;width:300px;height:500px;background-color:#eeeeee;border-radius:5px;padding:1%;font-family:"Open Sans";font-size:0.8rem}.card figure{height:50%;overflow:hidden}.card figure img{border-radius:5px}.card div{box-shadow:inset 0px 0px 10px 1px #0000003a;color:#000;width:100%;font-size:20px;background-color:#eeeeee;height:45%;display:block;border-radius:5px;padding:1%;overflow:hidden}.card div h1{font-size:1.2rem;text-align:left;margin-bottom:5px}.card div p{font-size:0.9rem;text-indent:5px;text-align:left}

